<script>
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'NhifClaimForm',
  props: {
    admission: {
      type: Object,
      default: () => {
      },

    },
  },
  data () {
    return {

      form: new window.Form({
        nhif_claim_auto: true,
        admission_id: '',
        nhif_quantity: 1,
        nhif_amount: 0,
        nhif_item_id: '',
        claim_no: 0,
        nhif_no: '',
        claim_period: '',
      }),

      items: [],
    }
  },

  watch: {
    'form.nhif_item_id': function (itemId) {
      if (itemId) {
        const vm = this

        const rebateItem = this.items.find((item) => item.id === itemId)

        if (this.admission.nhif_amount === 0) {
          if (this.admission.invoice.cash_credit === 0) {
            this.form.nhif_amount = rebateItem.cash_price.charge_value * 0.01
          }

          if (this.admission.invoice.cash_credit === 1) {
            this.form.nhif_amount = rebateItem.scheme_price.charge_value * 0.01
          }
        }
      }
    },
  },
  methods: {
    onModalShow () {
      setTimeout(() => {
        this.form.admission_id = this.admission.id
        this.form.claim_no = this.admission.claim_no ?? 0
        this.form.claim_period = this.admission.claim_period
        this.form.nhif_no = this.admission.nhif_no
        this.form.nhif_item_id = this.admission.nhif_item_id ?? ''
        this.form.nhif_amount = this.admission.nhif_amount ? this.admission.nhif_amount * 0.01 : 0
      }, 200)

      this.findRebateItems()
    },

    findRebateItems () {
      this.$httpClient.get('/datatable/products', {
        params: {
          filter: {
            rebate: true,
          },
        },
      }).then(({ data }) => {
        this.items = data.data

        if (data.data.length === 1) {
          this.form.nhif_item_id = data.data[0].id
        }
      }).catch()
    },

    submitClaim () {
      this.form.post('/nhif-claim')
        .then(() => {
          this.$toast.success('The claim process completed successfully')

          this.$emit('claim:submitted')

          this.$bvModal.hide('nhif-claim-modal')
        }).catch()
    },
  },
})
</script>

<template>
  <div>
    <b-modal
      id="nhif-claim-modal"
      title="Discharge Form"
      lazy
      no-close-on-backdrop
      :no-enforce-focus="true"
      hide-header-close
      modal-class="modal-blur"
      content-class="bg-white rounded-3"
      footer-class="d-flex justify-content-between border-top"
      @show="onModalShow"
    >
      <template #modal-title>
        <span v-if="admission">{{ admission.patient.name }}</span>
      </template>
      <form v-if="admission" id="claimForm" class="align-items-center" @submit.prevent="submitClaim">
        <div class="mb-3 row">
          <label class="col-4 col-form-label required">Admission Date</label>
          <div class="col-8">
            <date-picker :default-date="admission.date" disabled :value="admission.date" />
          </div>
        </div>
        <div class="mb-3 row">
          <label class="col-4 col-form-label required">Discharge Date</label>
          <div class="col-8">
            <date-picker :default-date="admission.discharged_at" disabled :value="admission.discharged_at" />
          </div>
        </div>
        <div class="mb-3 row">
          <label class="col-4 col-form-label required">No of Days</label>
          <div class="col-8">
            <input type="number" class="form-control" :value="admission.bed_days" disabled>
          </div>
        </div>
        <div class="mb-3 row">
          <label class="col-4 col-form-label">Rebate Product</label>
          <div class="col-8">
            <v-select
              v-model="form.nhif_item_id"
              :disabled="admission.rebate_date !== null"
              label="name"
              :options="items"
              :clearable="false"
              :reduce="option => option.id"
            />

            <span
              v-if="form.hasError('nhif_item_id')" class="text-danger"
              v-text="form.getError('nhif_item_id')"
            />
          </div>
        </div>
        <div class="mb-3 row">
          <label class="col-4 col-form-label">Amount & Qty</label>
          <div class="col-4">
            <medic-money
              :disabled="admission.rebate_date !== null"
              :value="form.nhif_amount"
              @input="function(amount) {
                form.nhif_amount = amount
              }"
            />
            <span
              v-if="form.hasError('nhif_amount')" class="text-danger"
              v-text="form.getError('nhif_amount')"
            />
          </div>

          <div class="col-4">
            <input v-model="form.nhif_quantity" type="number" class="form-control" :disabled="admission.rebate_date !== null">
            <span
              v-if="form.hasError('nhif_quantity')" class="text-danger"
              v-text="form.getError('nhif_quantity')"
            />
          </div>
        </div>
        <!--        <div class="mb-3 row">-->
        <!--          <label class="col-4 col-form-label pt-0" />-->
        <!--          <div class="col-8">-->
        <!--            <label class="form-check">-->
        <!--              <input-->
        <!--                v-model="form.nhif_claim_auto" class="form-check-input" type="checkbox"-->
        <!--                :checked="form.nhif_claim_auto"-->
        <!--              >-->
        <!--              <span class="form-check-label">Autogenerate NHIF Claim Number</span>-->
        <!--            </label>-->
        <!--          </div>-->
        <!--        </div>-->
        <!--        <div class="mb-4 row">-->
        <!--          <label class="col-4 col-form-label pt-0">NHIF Claim Number</label>-->
        <!--          <div class="col-4">-->
        <!--            <input-->
        <!--              v-model="form.claim_period" type="text" class="form-control"-->
        <!--              :disabled="form.nhif_claim_auto"-->
        <!--            >-->
        <!--          </div>-->
        <!--          <div class="col-4">-->
        <!--            <input-->
        <!--              v-model="form.claim_no" type="text" class="form-control"-->
        <!--              :disabled="form.nhif_claim_auto"-->
        <!--            >-->
        <!--          </div>-->
        <!--        </div>-->
        <div class="row">
          <label class="col-4 col-form-label pt-0">Membership Number</label>
          <div class="col-8">
            <input v-model="form.nhif_no" type="text" class="form-control">
          </div>
        </div>
      </form>

      <template #modal-footer="{cancel}">
        <button
          class="btn btn-light me-2"
          :disabled="form.processing"
          @click="cancel"
        >
          Close
        </button>

        <button
          form="claimForm"
          class="btn btn-primary"
          :disabled="form.processing"
        >
          Submit
        </button>
      </template>
    </b-modal>
  </div>
</template>

